
.App {
  text-align: center;
}

.box {
  border: 1px solid #d4d4d4;;
  border-radius: 5px;
  padding: 25px;
  width: 480px;
  background: #f1eeee;
  position: absolute;
  left: 33%;
  top: 35%;  
}

#text {
  font-weight: 900;
  font-size: 1.5em;
}

#author {
  display: -ms-flexbox;
  display: flex;
  float: right;
  margin: 0;
}

.buttons-container {
  padding-top: 40px;
}

.twitter {
  float: left;
}

.next-quote {
  float: right;
  padding: 10px;
  border-radius: 5px;
  border: 0;
  color:#fff;
  background-color: rgb(123, 195, 189);
}

.fa-twitter-square{
  color: black;
  font-size: 2.5em;
}

.fa-quote-left{
  font-size: 2em;
}


